import pubfooter from '@/components/pubfooter/pubfooter'
import axios from 'axios'

export default {
  name: 'searchresult',
  data: function() {
    return {
      loadingShow: false,
      busy: true, // 是否正在加载过程中
      nowPage: 1,
      keyword: null,
      categoryId: 0,
      allData: {},
      isNoData: false,
      noDataShow: false,
      imgData: [],
      baoyou: 0,
      zonghe: 0,
      sort: '', // 排序名称
      sort_sc: '', // 升序或者降序
      smallListName: 'zonghe',
      isClickPrice: true // 点击价格默认升序排列
    }
  },
  created() {
    if (this.$route.query.keyword) {
      this.keyword = this.$route.query.keyword
      this.categoryId = parseInt(this.$route.query.category)
      // this.baoyou = parseInt(this.$route.query.post)
      console.log('我是刚进来时的', this.keyword, this.categoryId)
      this._getData()
    } else {
      // 没有搜索关键词，直接重置路由到搜索页
      this.$router.push({path: 'search'})
    }
  },
  deactivated () {
    this.$destroy()
  },
  watch: {
    '$route' (to, from) { // 监听路由是否变化
      if (to.name === 'searchresult') {
      console.log('路由监听到了')
        this.keyword = this.$route.query.keyword
        this.categoryId = this.$route.query.category
        // this.baoyou = this.$route.query.post
        if (!!this.keyword) {
          this._getData()
        }
        /* if (this.bigId !== parseInt(this.$route.query.id) && (this.$route.query.id || this.$route.query.id === 0)) {// 判断条件1  判断传递值的变化
          this.bigId = parseInt(this.$route.query.id)
          this.categoryId = 0
          this._initCanshu()
          this._getData()
        } */
      }
    }
  },
  methods: {
    loadMore() {
      console.log('重复加载', this.busy)
      this.loadingShow = true
      this.busy = true
      // 官方示例中延迟了1秒，防止滚动条滚动时的频繁请求数据
      setTimeout(() => {
        // 这里请求接口去拿数据，实际应该是调用一个请求数据的方法
        this.nowPage++
        this._getData()
      }, 1000)
    },
    changeCategoryId(id) {
      this.categoryId = id
      this.nowPage = 1
      this.imgData = []
      this.baoyou = 0
      this.sort = ''
      this.sort_sc = ''
      this.isClickPrice = true
      this.nowPage = 1
      this.smallListName = 'zonghe'
      this.changeRouter()
    },
    changeSort(str) {
      console.log(str)
      this.imgData = []
      this.smallListName = str
      switch (str) {
        case 'post': // 点击包邮
          this.baoyou = 1
          this.sort = ''
          this.sort_sc = ''
          this.isClickPrice = true
          this.nowPage = 1
          break
        case 'zonghe':
          this.baoyou = 0
          this.sort = ''
          this.sort_sc = ''
          this.isClickPrice = true
          this.nowPage = 1
          break
        case 'sales_sum':
          this.baoyou = 0
          this.sort = 'sales_sum'
          this.sort_sc = 'desc'
          this.isClickPrice = true
          this.nowPage = 1
          break
        case 'shop_price':
          this.nowPage = 1
          if (this.isClickPrice) { // 升序排列
            this.baoyou = 0
            this.sort = 'shop_price'
            this.sort_sc = 'asc'
            this.isClickPrice = false
          } else { // 降序排列
            this.baoyou = 0
            this.sort = 'shop_price'
            this.sort_sc = 'desc'
            this.isClickPrice = true
          }
      }
      this._getData()
    },
    changeRouter() {
      this.$router.push({path: 'searchresult',
      query: {
        category: this.categoryId,
        keyword: this.keyword
      }})
    },
    _getData() {
      const url = '/Mobile/Search/dosearch.html'
      console.log(this.categoryId, this.nowPage)
      axios.get(url, {
        params: {
          keyword: this.keyword,
          p: this.nowPage,
          category: this.categoryId,
          post: this.baoyou,
          sort: this.sort,
          sort_sc: this.sort_sc
        }
      })
        .then((res) => {
          console.log(res)
          this.$store.commit('LOADING_SHOW', false)
          if (res.status === 200) {
            this.allData = res.data
            this.totalPage = this.allData.totalPages
            this.imgData = [...this.imgData, ...res.data.goods_list]
            console.log('1234', !this.totalPage)
            if (!this.totalPage) {
              this.isNoData = true
              this.busy = true
              this.noDataShow = true
            } else {
              this.isNoData = false
              this.busy = false
              this.noDataShow = false
              this.loadingShow = false
              if (this.nowPage >= this.totalPage) {
                this.busy = true
                this.loadingShow = false
                this.noDataShow = true
              }
            }
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    goEquipDetail(id) {
      this.$router.push({path: 'equipdetails', query: { id: id }})
    }
  },
  components: {
    pubfooter
  }
}



